.form {
  display: flex;
  width: 50%;
  justify-content: space-evenly;
}
.gauche {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  /* border: 1px red solid */
}
.email,
.sujet {
  //   color: blanchedalmond;
  font-size: 1.3rem;
}
input {
  height: 30px;
  width: 200px;
}
.button {
  background: black;
  color: blanchedalmond;
  font-size: 0.9rem;
  justify-content: center;
  width: 100px;
  border: 1px blanchedalmond solid;
  box-shadow: blanchedalmond 3px -2px 7px 0px;
  border-radius: 10px;
}
.message {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.3rem;
}
label {
  color: blanchedalmond;
  margin: 10px;
}

@media screen and (max-width: 900px) {
  .form {
    display: block;
    width: 100%;
  }
  .gauche {
    display: block;
    text-align: center;
    position: relative;
  }
  .email,
  .sujet {
    color: blanchedalmond;
    font-size: 1rem;
    padding-bottom: 10px;
  }
  .button {
    background: black;
    color: blanchedalmond;
    font-size: 0.9rem;
    position: absolute;
    transform: translateX(-50%) translateY(-50%);
    top: 250px;
    left: 50%;
    width: 80px;
    border: 1px blanchedalmond solid;
    box-shadow: blanchedalmond 3px -2px 7px 0px;
    border-radius: 10px;
  }
  .message {
    display: block;
    text-align: center;
    font-size: 1rem;
  }
  textarea {
    height: 100px;
  }
  label {
    color: blanchedalmond;
  }
}
