.contact {
  .logo {
    text-align: center;
    margin: 70px auto;
    img {
      width: 20%;
    }
  }
  .paragraphe {
    margin-top: 100px;
    text-align: center;
  }
  p {
    text-align: center;
    font-size: 1.5rem;
    color: rgb(252, 217, 165);
    position: relative;
    margin: 10px;
  }
  h2 {
    text-align: center;
    font-size: 1.5rem;
    color: red;
    margin-top: 80px;
  }

  h3 {
    text-align: center;
    font-size: 1.5rem;
    color: rgb(252, 217, 165);
    /* margin-top: 30px; */
  }
  .plan {
    display: flex;
    justify-content: center;
    margin: 50px;
  }
  .planmobile {
    display: none;
  }
  .tel {
    font-size: 1.8rem;
  }

  .logofb {
    text-align: center;
  }
  .logofb img {
    width: 50px;
    transform: translateY(30%) translateX(15%);
  }
  .formulaire {
    display: flex;
    justify-content: center;
    margin: 40px;
  }

  @media screen and (max-width: 900px) {
    .logo {
      margin: 20px auto;
      width: 100%;
    }
    img {
      width: 70%;
    }
    p {
      font-size: 0.9rem;
      margin-top: 20px;
    }
    h3 {
      font-size: 1.2rem;
    }
    .tel {
      font-size: 1rem;
    }
    .plan {
      display: none;
    }
    .planmobile {
      display: flex;
      justify-content: center;
    }
    .formulaire {
      display: block;
      justify-content: center;
    }
  }
}
