.logocomplet {
  text-align: center;
  position: relative;
  top: 100px;

  img {
    width: 250px;
  }
  h1 {
    font-family: "allura";
    font-size: 3.5rem;
    background: linear-gradient(#b58114, #f8e891);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
