.atelier {
  .logo {
    width: 100%;
    img {
      width: 200px;
    }
    h1 {
      display: none;
    }
  }
  p {
    position: relative;
    top: 120px;
    text-align: center;
    font-family: "lora";
    color: blanchedalmond;
    font-size: 1.2rem;
  }
  h2 {
    text-align: center;

    font-size: 1.5rem;
    color: red;
    margin-top: 80px;
  }
  h3 {
    color: rgb(252, 217, 165);
    text-align: center;
    font-size: 1.2rem;
  }
  .tons {
    position: relative;
    width: 100%;
    top: 250px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .tons img {
    border: 2px gray solid;
    width: 600px;
  }
  .videos {
    position: relative;
    width: 100%;
    top: 250px;
    display: flex;
    flex-direction: column;
  }
  .anglebrique {
    margin: 0 auto;
    width: 40vw;
  }
  .photos {
    text-align: center;
    position: relative;
    width: 100%;
    top: 250px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .photos img {
    width: 400px;

    margin: 25px;
    border: 2px gray solid;
  }

  @media screen and (max-width: 800px) {
    .logopage {
      top: 30px;
    }
    .anglebrique {
      margin: 0 auto;
      width: 70vw;
    }
    h3 {
      font-size: 1rem;
    }
    p {
      font-size: 0.9rem;
      margin: 10px;
    }
    .photos img {
      width: 200px;
    }
    .tons {
      top: 140px;
      img {
        width: 300px;
      }
    }
  }
}
